#btn-new-customer {
  padding: 5px;
  height: fit-content;
  min-width: fit-content;
  box-shadow: 1px 1px 2px rgba(0,0,0,0.7);

}
#customerModal {
  box-shadow: 1px 1px 2px rgba(0,0,0,0.7);
}
#heading {
  border-bottom: 1px solid lightgrey;
}
#manage-customer-main-div {
  width: 100%;
  height: 100%;
}
#modal-header{
  background-color: #dde7fc;
}
#modal-label {
  font-weight: bold;
  font-size: 1.4rem;


}
#modal-body {
  div {
    label{
      font-weight: bold;
      font-size: 1.2rem;
    }
  }
}
#txt-search-customer{
  width: 100%;
  text-align: center;
  border: 1px solid lightgrey;
  border-radius: 10px;
  padding: 5px;
  margin: 20px 10px 10px;
}
#txt-search-customer:focus{
  border: 1px solid rgba(81, 167, 251, 0.88);
}
table {
  box-shadow: 1px 1px 2px rgba(0,0,0,0.7);

}


